<template>
	<div>
		
		<el-row :gutter="24" type="justify">
			<el-col :span="2" :push="16"><el-button  @click="orderAdd()">加菜</el-button></el-col>
			<el-col :span="2" :push="16"><el-button  @click="payTheBill()">买单</el-button>	</el-col>
			<el-col :span="2" :push="16"><el-tag >{{this.orderVO.price}}</el-tag></el-col>
		</el-row>
		
		
		<el-table :data="orderTableData" style="width: 98%">
			<el-table-column label="菜名" width="180">
			  <template slot-scope="scope">
				  <el-tag>{{scope.row.footName}}</el-tag>
			  </template>
			</el-table-column>
			<el-table-column label="状态">
				<template slot-scope="scope">
					<el-tag size="medium" v-if="scope.row.status==0">退菜</el-tag>
					<el-tag size="medium" v-if="scope.row.status==1">准备</el-tag>
					<el-tag size="medium" v-if="scope.row.status==2">加菜</el-tag>
					<el-tag size="medium" v-if="scope.row.status==3">已上</el-tag>
				</template>
			</el-table-column>
			<el-table-column label="价格">
				<template slot-scope="scope">
					<el-tag>{{scope.row.price}}</el-tag>
				</template>
			</el-table-column>
			<el-table-column label="数量">
				<template slot-scope="scope">
					<el-tag>{{scope.row.num}}</el-tag>
				</template>
			</el-table-column>
			<el-table-column label="备注">
				<template slot-scope="scope">
					<el-tag>{{scope.row.remark}}</el-tag>
				</template>
			</el-table-column>
			<el-table-column label="操作">
			  <template slot-scope="scope">
					<el-button
						style="margin-top: 4px;margin-left: 10px;"
						size="mini" 
						@click="orderSkuHand(scope.$index, scope.row,0)" v-if="scope.row.status==1 || scope.row.status ==2">退菜</el-button>
					<el-button
						style="margin-top: 4px;"
						size="mini"
						@click="orderSkuHand(scope.$index, scope.row,3)" v-if="scope.row.status==1 || scope.row.status ==2">上菜</el-button>
			  </template>
			</el-table-column>
		</el-table>
		<el-dialog
			title="提示"
			:visible.sync="dialogVisible"
			width="60%">
			<el-form  :model="payForm" label-width="80px"  ref="ruleForm">
			  <el-form-item label="桌号" >
				<el-tag >{{payForm.tableId}}</el-tag>
			  </el-form-item>
			  <el-form-item label="价格" >
				<el-tag  placeholder="价格">{{payForm.price}}</el-tag>
			  </el-form-item>
			  <el-form-item label="实收" >
				  <el-input v-model="payForm.realPrice" placeholder="折扣价"></el-input>
			  </el-form-item>
			  <el-form-item label="支付方式">
				   <el-radio-group v-model="payForm.payType">
				       <el-radio :label="0">微信</el-radio>
				       <el-radio :label="1">支付宝</el-radio>
				       <el-radio :label="2">现金</el-radio>
				     </el-radio-group>
			  </el-form-item>
			  <el-form-item>
				<el-button type="primary" @click="onSubmit">买单</el-button>
				<el-button type="primary" @click="cancel">取消</el-button>
			  </el-form-item>
			</el-form>
		</el-dialog>  	  
	</div>
</template>

<script>
	import {getAllCategory,
	changeOrderSkuState,
	listOrderSkuByOrderId,
	finishOrder} from '../../api/index.js'
	
	export default {
		data(){
			return {
				dialogVisible:false,
				payForm:{
					payType:0,
					realPrice:0,
					price:0,
					tableId:0,
				},
				orderVO:{
					
				},
				orderTableData:[
					{"id":18,
					"orderId":23,
					"footId":11,
					"footName":"炒娃娃菜",
					"num":2,
					"status":1,
					"price":100,
					"realPrice":0,
					"createTime":"2019-10-25 15:10:57",
					"updateTime":"2019-10-25 15:10:57",
					"createBy":"1",
					"remark":"微辣"},
				],
			}
		},
		methods:{
			orderSkuHand(index,row,tates){
				
				changeOrderSkuState({orderSkuId:row.id,orderSkuState:tates}).then(r=>{
					// console.log(JSON.stringify(r))
					listOrderSkuByOrderId({orderId:row.orderId}).then(r=>{
						console.log(JSON.stringify(r.data))
						this.orderTableData= r.data.list
						if(tates ==0){
							this.orderVO = r.data
						}
					})
					
				})
			},
			orderAdd(){
				this.$router.push({name:'OrderAdd',params:{orderVO:this.orderVO}})
			},
			payTheBill(){
				this.dialogVisible = true;
				this.payForm.tableId = this.orderVO.tableId
				this.payForm.price = this.orderVO.price
				this.payForm.realPrice = this.orderVO.price
			},
			onSubmit(){
				this.dialogVisible = false;
				 finishOrder({orderId:this.orderVO.id,payType:this.payForm.payType,realMoney:this.payForm.realPrice}).then(r=>{
					// console.log(JSON.stringify(r))
					this.$router.push({name:'Order'}) 	 
				 })
			},
			cancel(){
				this.dialogVisible =false;
			}
		},
		created:function(){
			if(this.$route.params){
				// console.log(JSON.stringify(this.$route.params.orderVO))
				this.orderTableData = this.$route.params.orderVO.list;
				this.orderVO = this.$route.params.orderVO
			}
		}
	}
</script>

<style style scss scoped>
	.table{
		width: 25%;
		height: 20%;
		background: #42B983;
	}
	
</style>
